<template>
  <div>
    <!-- ============================================================== -->
    <!-- Preloader - style you can find in spinners.css -->
    <!-- ============================================================== -->
    <div class="preloader" v-bind:class="{hide_elem: show_preloader}">
        <svg class="circular" viewBox="25 25 50 50">
            <circle class="path" cx="50" cy="50" r="20" fill="none" stroke-width="2" stroke-miterlimit="10" /> </svg>
    </div>
    <!-- ============================================================== -->
    <!-- Main wrapper - style you can find in pages.scss -->
    <!-- ============================================================== -->
    <section id="wrapper" class="login-register login-sidebar" :style="{backgroundImage: `url(${require('@/assets/images/background/login-register.jpg')})` }">
  <div class="login-box card">
    <div class="card-body">
      <form class="form-horizontal form-material" id="loginform">
        <router-link to="javascript:void(0)" class="text-center db"><img src="../assets/images/logo-icon.png" alt="Home" /><br/><img src="../assets/images/logo-text.png" alt="Home" /></router-link>  
        
        <div class="form-group mt-5">
          <div class="col-xs-12">
            <input class="form-control" v-model="email" type="text" required="" placeholder="Логин" v-bind:class="{error: error1}">
          </div>
        </div>
        <div class="form-group">
          <div class="col-xs-12"> 
            <input class="form-control" v-model="pass" type="password" required="" placeholder="Пароль" v-bind:class="{error: error2}">
          </div>
        </div>
        <div class="form-group">
          <div class="col-md-12">
            <div class="checkbox checkbox-primary float-left pt-0">
              <input id="checkbox-signup" type="checkbox">
              <label for="checkbox-signup"> Запомнить меня </label>
            </div>
            <span v-on:click="rec_pass"><router-link to="" id="to-recover" class="text-dark float-right"><i class="fa fa-lock mr-1"></i> Забыли пароль?</router-link></span> </div>
        </div>
        <span :style="{color: 'red'}" v-if="error_response == 'This user is None'" v-bind:class="{hide_elem: error3}">Пользователь не зарегестрирован!</span>
        <span :style="{color: 'red'}" v-bind:class="{hide_elem: error4}">Сервер не отвечает!<br> Повторита запрос позже!</span>

        <div class="form-group text-center mt-3">
          <div class="col-xs-12">
            <span v-on:click="loginFunc"><router-link to="" class="btn btn-info btn-lg btn-block text-uppercase waves-effect waves-light" >Войти</router-link></span>
          </div>
        </div>
        <div class="form-group mb-0">
          <div class="col-sm-12 text-center">
            <p>Нет аккаунта? <router-link to="/register" class="text-primary ml-1"><b>Зарегистрироваться</b></router-link></p>
          </div>
        </div>
      </form>
      <form class="form-horizontal" v-bind:class="{hide_elem: recuvery_pass}">
        <div class="form-group ">
          <div class="col-xs-12">
            <h3>Восстановление пароля</h3>
            <p class="text-muted">Введите e-mail и мы вышлем на него инструкции по восстановлению </p>
          </div>
        </div>
        <div class="form-group ">
          <div class="col-xs-12">
            <input class="form-control" type="text" required="" placeholder="Email">
          </div>
        </div>
        <div class="form-group text-center mt-3">
          <div class="col-xs-12">
            <button class="btn btn-primary btn-lg btn-block text-uppercase waves-effect waves-light" type="submit">Восстановить</button>
          </div>
        </div>
      </form>
    </div>
  </div>
  </section>
</div>
</template>

<style code="css">
    @import url('~@/assets/style/css/style.css');
    @import url('~@/assets/style/css/colors/red-dark.css');
    @import url('~@/assets/plugins/bootstrap/css/bootstrap.min.css');
    @import url('~@/assets/plugins/datatables.net-bs4/css/dataTables.bootstrap4.css');

</style>

<script>
    import {login} from '@/service/login'
    import Cookies from "js-cookie"


    export default {
        data() {
            return {
                show_preloader: true,

                email: '',
                pass: '',

                error1: false,
                error2: false,
                error3: true,
                error4: true,

                error_response: '',

                recuvery_pass: true,
            }
        },
        methods: {
            loginFunc() {
                let data = {
                    email: this.email,
                    password: this.pass}


                if (this.email.length === 0) {
                    this.error1 = true;
                    setTimeout(() => {this.error1 = false}, 2000)
                }
                if (this.pass.length === 0) {
                    this.error2 = true;
                    setTimeout(() => {this.error2 = false}, 2000)
                }

                if (!this.error1 && !this.error2) {
                  login(data).then(res => {
                        this.show_preloader = false;
                        this.$router.push('/index')
                    })
                    .catch(err => {
                      try {
                        this.email = '';
                        this.pass = '';
                        this.error_response = err.response.data.error;
                        this.error3 = false;
                        setTimeout(() => {this.error3 = true}, 2000)
                      } catch {
                        this.error4 = false;
                        setTimeout(() => {this.error4 = true}, 2000)
                      }
                    })
                }
            },
            rec_pass() {
              this.recuvery_pass = !this.recuvery_pass;
            }
        },

    }
</script>